import uniIcon from '@/components/uni-icon/uni-icon.vue';
import { post } from '@/api/user.js';
import { api } from '@/config/common.js';
export default {
	data() {
		return {
			loading: false,
			stop: false,
			miao: 60,
			oldpassword: '',
			password: '',
			re_password: '',
			title: '修改密码'
		};
	},
	components: { uniIcon },
	methods: {
		handleNavbarClickLeft() {
			uni.navigateBack({
				delta: 1
			});
		},
		login_action() {
			uni.reLaunch({
				url: '/pages/my/login/login'
			});
		},

		handleReg() {
			const { oldpassword, password, re_password } = this;
			if (oldpassword.length == 0) {
				this.$api.msg('原登录密码不能为空！');
				return;
			}
			if (password.length == 0) {
				this.$api.msg('新登录密码不能为空！');
				return;
			}
			if (re_password.length == 0) {
				this.$api.msg('重复密码不能为空！');
				return;
			}
			if (password != re_password) {
				this.$api.msg('两次密码不一致！');
				return;
			}
			const sendData = {
				password,
				oldpassword
			};
			this.loading = true;
			post(api.ChangePassword, sendData)
				.then(res => {
					if (res.status == 200 && res.data.returnCode == '0000') {
						this.$api.msg('密码修改成功！');
						uni.navigateBack({
							delta: 1
						});
					} else {
						this.$api.msg(res.data.returnMessage);
					}
					this.loading = false;
				})
				.catch(error => {
					this.loading = false;
					this.$api.msg('请求失败fail');
				});
		},
		send() {
			const mobile = this.mobile;
			this.settime(60);
			if (mobile.length != 11) {
				this.$api.msg('手机号错误');
				return;
			}
		},
		settime(smiao) {
			const that = this;
			const miao = that.miao;
			if (miao == 0) {
				that.miao = 0;
				this.stop = false;
			} else {
				setTimeout(() => {
					smiao--;
					this.miao = smiao;
					that.settime(smiao);
				}, 1000);
			}
		}
	}
};
